    fetchByAccountNum(accNum){
      this.cashdepositForm.controls.transactionAmount.reset();
      this.cashdepositForm.controls.totalTransactionAmount.setValue(this.cashdepositForm.controls.totalChargeAmount.value);
      console.log("Account Numebr",accNum);
      // this.isLoading = true;
      this.apiService.fetchByaccNum(accNum).subscribe((res: any) => {
        console.log('Account Number Rsp', res);
        this.getChargeDetails(accNum,'');
        // this.isLoading = false;
        this.userName = res.accHolderName;
        this.accountBal = res.accBalance;
        this.custmerID = res.customerId;
        this.accountNumber = res.accountId;
        this.accountCurrency = res.accountCurrency;
        this.curr = getCurrencySymbol(res.currency, "narrow");
        this.cashdepositForm.controls.accountType.setValue(res.accountType);
        this.cashdepositForm.controls.accountCurrency.setValue(res.currency);
        this.cashdepositForm.controls.accountBranch.setValue(res.accountBranch);
        this.cashdepositForm.controls.holderName.setValue(this.userName); 
        this.getCountrynameValues();
        this.currencyImage = 'currency-flag-'+res.currency.toLowerCase();
        this.currencyImage = 'currency-flag-'+res.currency.toLowerCase();
        this.cashdepositForm.controls.transactionCurrency.setValue(res.currency);
        this.custAmountBal(accNum);
        this.cashdepositForm.controls.transactionBranch.setValue('Kasturi Nagar');
        this.apiService.fetchByaccDeatilsoncustId(res.customerId).subscribe((resp: any) => {
          this.phonenum = resp[0].phoneNumber;
          localStorage.setItem('otpPhoneNum', this.phonenum);
        }) 
    });
  
  }
