formatAmount(amount) {
  if(amount){
    return amount && amount.toString().split('.')[0].length > 3 ? amount.toString().substring(0,amount.toString().split('.')[0].length-3).replace(/\B(?=(\d{2})+(?!\d))/g, ",") + "," + amount.toString().substring(amount.toString().split('.')[0].length-3): amount.toString();
   }
  }
  cashWithdrawlData(){
    this.apiService.getAppointmentId(this.appointmentId.data.appointmentId).subscribe((resp : any)=> {
      console.log(resp);
      this.cashData=resp.data;
      this.custID=resp.data.customerId;
      this.ifQRvariable = true;
      this.chequewithdrawForm.controls.accountType.setValue(resp.data.accountType);
      this.chequewithdrawForm.controls.accountBranch.setValue(resp.data.accountBranch);
      this.chequewithdrawForm.controls.accountNumber.setValue(resp.data.accountNumber);
      this.chequewithdrawForm.controls.chequeNumber.setValue(resp.data.chequeNumber);
      this.getChargeDetails(resp.data.accountNumber,resp.data.transactionAmount);
      this.chequewithdrawForm.controls.accountCurrency.setValue(resp.data.accountCurrency);
      this.chequewithdrawForm.controls.transactionCurrency.setValue(resp.data.transactionCurrency);
      this.chequewithdrawForm.controls.transactionAmount.setValue(resp.data.transactionAmount);
    
      this.currencyImage = 'currency-flag-'+resp.data.transactionCurrency.toLowerCase();
      this.currencyImage = 'currency-flag-'+resp.data.accountCurrency.toLowerCase();
  
  
    });
  }
