import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Observable, Subject } from 'rxjs';
import { WebcamImage } from 'ngx-webcam';
import { Router } from '@angular/router';
@Component({
  selector: 'app-ocr-scan-page',
  templateUrl: './ocr-scan-page.component.html',
  styleUrls: ['./ocr-scan-page.component.scss']
})
export class OcrScanPageComponent implements OnInit {
  flag: boolean = false;
  retakeFlag: boolean = true;
  submitFlag: boolean = true;
  constructor(public dialogRef: MatDialogRef<OcrScanPageComponent>,private router: Router) { }

  private trigger: Subject<any> = new Subject();
  public webcamImage: WebcamImage;
  private nextWebcam: Subject<any> = new Subject();
  sysImage = '';
  ngOnInit() {}
  getSnapshot(){
    this.flag = true;
    this.trigger.next();
  }
  captureImg(webcamImage: WebcamImage){
    this.webcamImage = webcamImage;
    this.sysImage = webcamImage?.imageAsDataUrl;
    sessionStorage.setItem("sysImage",this.sysImage);
    console.info('got webcam image', this.sysImage);
  }
  public get invokeObservable(): Observable<any> {
    return this.trigger.asObservable();
  }
  public get nextWebcamObservable(): Observable<any> {
    return this.nextWebcam.asObservable();
  }
  retake(){
    this.flag = false;
    this.retakeFlag = false;
    this.submitFlag = false;
  }
  submit(){
    this.dialogRef.close();
    sessionStorage.getItem('sysImage');
  }
}
