import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { DialogService } from 'app/shared/services/Dialog/dialog.service';

interface Testing {
  value: string;
  viewValue: string;
}


@Component({
  selector: 'app-utility-billpayment',
  templateUrl: './utility-billpayment.component.html',
  styleUrls: ['./utility-billpayment.component.scss']
})
export class UtilityBillpaymentComponent implements OnInit {
  utilityBillPaymentForm: FormGroup;
  utilityBillpayment : any = 'Utility Bill Payment';
  constructor(private router : Router, private formBuilder: FormBuilder,  private dialogService : DialogService) { }

  ngOnInit(): void {
    this.utilityBillPaymentForm = this.formBuilder.group({
      billerName:['', [Validators.required]],
      amount:['', [Validators.required]],
      payFromAcc:['', [Validators.required]],
      billDate:['', [Validators.required]],
      billNumber:['', [Validators.required]],
    });
  }
  goToServiceScreens(){
    this.router.navigateByUrl('/others/kioskHome');
  }
  goToAuthenticate(){
    this.dialogService.authenticationDialog();
  }

  testing: Testing[] = [
    { value: 'Demo', viewValue: 'Demo 1' },
    { value: 'Demo', viewValue: 'Demo 2' },
    { value: 'Demo', viewValue: 'Demo 3' }
  ];

}
