import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { CaptureChequeDetailsComponent } from '../capture-cheque-details/capture-cheque-details.component';
@Component({
  selector: 'app-scan-your-cheque',
  templateUrl: './scan-your-cheque.component.html',
  styleUrls: ['./scan-your-cheque.component.scss']
})
export class ScanYourChequeComponent implements OnInit {

  constructor(private dialog: MatDialog,public dialogRef: MatDialogRef<ScanYourChequeComponent>,private router: Router) { }

  ngOnInit(){
  }

  close(){
    this.dialogRef.close();
    this.router.navigateByUrl('/others/cheq-deposit');
  }

  scan(){
    let dialogRef = this.dialog.open(CaptureChequeDetailsComponent, {
      width: '60%',
      height:'65%',
    });
     this.router.navigateByUrl('/others/cheq-deposit');
  }
}
