  gettingallTansactionDetails(phoneNumber) {
    
    this.phoneNumber = localStorage.getItem("phonenumFromLogin");
    this.apiService.gettingTransactionDetails(this.phoneNumber).subscribe((allTransactionResp : any)=> {
    console.log("All Transaction Details",allTransactionResp);
    this.accountCurrency = allTransactionResp.accountCurrency;
    this.userName = (allTransactionResp.firstName ? allTransactionResp.firstName + ' ' : ' ') + 
    (allTransactionResp.middleName ? allTransactionResp.middleName + ' ' : ' ') +
     (allTransactionResp.lastName ? allTransactionResp.lastName : ' ');
    this.cashwithdrawForm.controls.holderName.setValue(this.userName);
    // this.setBalance(allTransactionResp.custAccount);
    this.custID = allTransactionResp.customerId;
    console.log("Cust", this.custID);
    this.custAccountData = allTransactionResp.custAccount;
    this.accounttypeArray = [...this.custAccountData].filter(({accountType},index )=> 
    ![...this.custAccountData].map(obj=> obj.accountType).includes(accountType, index +1));
    console.log(this.accounttypeArray)
    for(let i = 0; i < this.custAccountData.length; i++){
      if(this.custAccountData[i].status == 'APPROVED'){
        this.accountNoList.push(this.custAccountData[i]);    
      }
    }
    console.log(this.accountNoList);
    if(this.custAccountData && this.accountNoList.length<=1){
      this.accountDropdown=false;
      this.hideDetails=true;
      this.hideAccNum=true;
      const accTyp = allTransactionResp.custAccount[0].accountType;
      this.cashwithdrawForm.controls.accountType.setValue(accTyp);
      this.custAccountNumber(this.accountNoList[0]);
    }else{
      this.accountDropdown=true;
      this.hideDetails=true;
      this.hideAccNum=true;
      const accTyp = allTransactionResp.custAccount[0].accountType;
      this.cashwithdrawForm.controls.accountType.setValue(accTyp);
      this.custAccountNumber(allTransactionResp.custAccount[0]);
    }
  
    });

    
  }
  getAccountType(event:any){
    this.cashwithdrawForm.controls.transactionAmount.reset();
    this.cashwithdrawForm.controls.totalTransactionAmount.setValue(this.cashwithdrawForm.controls.totalChargeAmount.value);
    this.accountNoList=[];
    this.isedit=false;
    this.accountNum1=[...this.custAccountData].filter((accounNum,index)=>{
      return accounNum.accountType==event;
    });
    // this.accountNoList=this.accountNum1
    // this.accountNoList=this.accountNum1.map((acccount)=>
    // {
    //   return acccount.accountId
    // });
    for(let i = 0; i < this.custAccountData.length; i++){
      if(this.custAccountData[i].status == 'APPROVED' && this.custAccountData[i].accountType == event){
        this.accountNoList.push(this.custAccountData[i].accountId);    
      }
    }
   // this.accountNoList.push(12233)
   console.log(this.accountNoList)
   
    if(this.accountNoList.length<=1){
      // this.cashwithdrawForm.controls.accountNumber.patchValue(this.accountNoList[0]);
      this.flag1=false; 
      this.getData(this.accountNoList[0]);
     
      this.cashwithdrawForm.controls.accountNumber.setValue(this.accountNoList[0]);
    }
    else{
      this.getData(this.accountNoList[0]);
      this.flag1=true;   
    }
    this.getChargeDetails(this.accountNoList[0],'');
  }
