import { DEFAULT_CURRENCY_CODE, NgModule } from '@angular/core';
import { CommonModule, CurrencyPipe, DecimalPipe } from '@angular/common';
import { AutoFocusDirective } from './auto-focus.directive';
import { SeparatorDirective } from './separator.directive';
import { TwoDigitDecimaNumberDirective } from './decimal.directive';
import { AlphabetOnlyDirective } from './alphabet-only.directive';
import { NumberDirective } from './numbers-only.directive';
import { TrimInputDirective } from './trim-input.directive';
import { SpecialTextDirective } from './special-text.directive';

const directives = [
  AutoFocusDirective,
  SeparatorDirective,
  TwoDigitDecimaNumberDirective,
  AlphabetOnlyDirective,
  NumberDirective,
  TrimInputDirective,
  SpecialTextDirective
]

@NgModule({
  declarations: [directives],
  imports: [
    CommonModule
  ],
  exports: directives,
  providers: [
    CurrencyPipe, 
    DecimalPipe,
    {provide: DEFAULT_CURRENCY_CODE, useValue: 'INR'}
  ]
})
export class DirectiveModule { }
