import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators  } from '@angular/forms';
import { Router } from '@angular/router';
import { DialogService } from 'app/shared/services/Dialog/dialog.service';

interface Testing {
  value: string;
  viewValue: string;
}

@Component({
  selector: 'app-loan-payment',
  templateUrl: './loan-payment.component.html',
  styleUrls: ['./loan-payment.component.scss']
})
export class LoanPaymentComponent implements OnInit {
  loanpaymentForm: FormGroup;
  loanPayment : any = 'Loan Payment';

  constructor(private router : Router,private formBuilder: FormBuilder, private dialogService : DialogService) { }

  ngOnInit(): void {
    this.loanpaymentForm = this.formBuilder.group({
      loanAccount:['', [Validators.required]],
      currentArrears:['', [Validators.required]],
      currentDue:['', [Validators.required]],
      principalOutstanding:['', [Validators.required]],
      payFromAccount:['', [Validators.required]],
      holderName:['', [Validators.required]],
      repaymentAmount:['', [Validators.required]], 
    });
  }
  goToServiceScreens(){
    this.router.navigateByUrl('/others/kioskHome');
  }
  goToAuthenticate(){
    this.dialogService.authenticationDialog();
  }

  testing: Testing[] = [
    { value: 'Demo', viewValue: 'Demo 1' },
    { value: 'Demo', viewValue: 'Demo 2' },
    { value: 'Demo', viewValue: 'Demo 3' }
  ];

}
