import { Component, OnInit } from '@angular/core';
import { NavigationExtras, Router } from '@angular/router';
export interface Section {
  title: string;
  id : number;
  flag: boolean;
  
}

@Component({
  selector: 'app-cheque-withdrawal1',
  templateUrl: './cheque-withdrawal1.component.html',
  styleUrls: ['./cheque-withdrawal1.component.scss']
})

export class ChequeWithdrawal1Component implements OnInit {
  flag: boolean = true;
  continue = "CONTINUE"
  active : any = {};
  folders: Section[] = [
    {
      title: "Cash Withdrawal",
      id : 1,
      flag : false,
      
    },
    {
      title: "Cash Deposit",
      id : 2,
      flag : false,
      
    },
    {
      title: "Cheque Deposit",
      id : 3,
      flag : false,
    },
    {
      title: "Cheque Withdrawal",
      id : 4,
      flag : false,
    },
    {
      title: "Forex Transaction",
      id : 5,
      flag : false,
    },
    {
      title: "Loan Payment",
      id : 6,
      flag : false,
    },
    {
      title: "Deposit Top Up",
      id : 7,
      flag : false,
    },
    {
      title: "Utility Bill Payment",
      id : 8,
      flag : false,
    },

  ];
  
  serviceTitle: any;

  constructor(private router : Router,) { }

  ngOnInit(): void {
  }
  trackByFn(index, item) {
    return index; //or item.id
  }
  goToService(index){
    if(this.active !== index){
      this.active = index;
      // console.log(this.active);
      this.folders.forEach(item => { item.flag = false});
    } 
    this.folders[index].flag = !this.folders[index].flag;
  }
  contiueButton(){
    console.log(this.active);
    const enableList = this.folders.filter(x => x.flag === true)
    console.log(enableList);
    if(enableList.length > 0 )
    return true;
    else 
    return false;
  }
  goToOperation(){
    this.router.navigateByUrl('/others/master');
  }

}
