import { Country } from '@angular-material-extensions/select-country';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators  } from '@angular/forms';
import { Router } from '@angular/router';
import { DialogService } from 'app/shared/services/Dialog/dialog.service';

interface Testing {
  value: string;
  viewValue: string;
}

@Component({
  selector: 'app-forex-transaction',
  templateUrl: './forex-transaction.component.html',
  styleUrls: ['./forex-transaction.component.scss']
})
export class ForexTransactionComponent implements OnInit {
  forexForm: FormGroup;
  countryFormControl = new FormControl();
  defaultValue: Country = {
    name: 'Deutschland',
    alpha2Code: 'DE',
    alpha3Code: 'DEU',
    numericCode: '276',
    callingCode: '+49'
  };
  forexTransaction : any = 'Forex Transaction';
  constructor(private router : Router, private formBuilder: FormBuilder,private dialogService : DialogService) { }

  ngOnInit(): void {
    this.forexForm = this.formBuilder.group({
      accountNumber:['', [Validators.required]],
      holderName:['', [Validators.required]],
      fxCurrency:[''],
      fxAmount:['', [Validators.required]],
      negotiatedRate:['', [Validators.required]],
      country: [
        {
          value: {
            name: 'Deutschland',
            alpha2Code: 'DE',
            alpha3Code: 'DEU',
            numericCode: '276',
            callingCode: '+49'
          },
          disabled: false
        }
      ]
    });

    this.forexForm
      .get('country')
      .valueChanges.subscribe((country) =>
      console.log(
        'this.countryFormGroup.get(\'country\').valueChanges',
        country
      )
    );

    this.countryFormControl.valueChanges.subscribe((country) =>
      console.log('this.countryFormControl.valueChanges', country)
    );

  }
  onCountrySelected($event: Country): void {
    console.log($event);
  }
  goToServiceScreens(){
    this.router.navigateByUrl('/others/kioskHome');
  }
  goToToken(){
     this.dialogService.tokenGenerationDialog();
  }

  testing: Testing[] = [
    { value: 'Demo', viewValue: 'Demo 1' },
    { value: 'Demo', viewValue: 'Demo 2' },
    { value: 'Demo', viewValue: 'Demo 3' }
  ];

}
