  omit_number(event) {
    var key;
    key = event.charCode;
    return ((key > 47 && key < 58));
  }
  totalAmountValue(cdForm) {
    this.a=this.chequedepositForm.value.transactionAmount;
    this.a=this.a.replace(/\,/g,''); // 1125, but a string, so convert it to number
    this.a=parseInt(this.a,10);


    this.chequedepositForm.value.totalAmount =
      ((cdForm.value.count2000 ? cdForm.value.count2000 * 2000 : 0) +
        (cdForm.value.count500 ? cdForm.value.count500 * 500 : 0) +
        (cdForm.value.count200 ? cdForm.value.count200 * 200 : 0) +
        (cdForm.value.count100 ? cdForm.value.count100 * 100 : 0) +
        (cdForm.value.count50 ? cdForm.value.count50 * 50 : 0) +
        (cdForm.value.count20 ? cdForm.value.count20 * 20 : 0) +
        (cdForm.value.count10 ? cdForm.value.count10 * 10 : 0) +
        (cdForm.value.count1 ? cdForm.value.count1 * 1 : 0)
        );
        console.log(this.chequedepositForm.value.totalAmount)
        console.log(this.chequedepositForm.value.transactionAmount);
        this.transamount=this.chequedepositForm.value.transactionAmount;
        this.totalAmount=this.chequedepositForm.value.totalAmount;
        this.chequedepositForm.controls.totalAmount.setValue(this.chequedepositForm.value.totalAmount);
        
  }
  OnInput(event: any) {
   this.transAmt= event.target.value;

     // comma separator using changeDetector
   const savedInput = this.transAmount;

   let lastCharIsPoint = false;
   if (savedInput.charAt(savedInput.length - 1) === '.') {
     lastCharIsPoint = true;
   }
   const num = savedInput.replace(/[^0-9.]/g, '');
   this.transAmt = Number(num) || '';
   this.transAmount = this.transAmt.toLocaleString('en-IN');
   if (lastCharIsPoint) {
     this.transAmount = this.transAmount.concat('.');
   }
   this.changeDef.detectChanges();
    this.balance= localStorage.getItem("AccountBal");
    if(parseFloat(this.balance) < parseFloat(this.transAmt)){
      console.log("Bigger")
      this.snack.open(`Transaction Amount should not exceed than Account Balance`, 'OK', {
        duration: 2000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
      this.enableContinue=false;
    }
    else{
      this.enableContinue=true;
    }
    this.totalTransAmount=Number(this.transAmt) + Number(this.chargeDetails?this.chargeDetails[0].amount:'');
    this.chequedepositForm.controls.totalTransactionAmount.setValue(this.totalTransAmount);
    this.cdr.markForCheck();
    this.cdr.detectChanges();
    }
    getChargeDetails(accountId,amount){
      this.apiService.fetchChargeDetails(accountId).subscribe(resp => {
    console.log(resp);
    this.chargeDetails=resp;
    this.chequedepositForm.controls.totalChargeAmount.setValue(this.chargeDetails?this.chargeDetails[0].amount:'');
      });
      if(this.cashData){
      setTimeout(() => {
        this.totalTransAmount=Number(this.cashData.transactionAmount) + Number(this.chargeDetails?this.chargeDetails[0].amount:'');
        this.chequedepositForm.controls.totalTransactionAmount.setValue(this.totalTransAmount);
      }, 1000);
      this.cdr.markForCheck();
      this.cdr.detectChanges();
    }
    else{
      console.log(this.chargeDetails)
      amount=0;
      console.log(amount);
      setTimeout(() => {
        this.totalTransAmount=Number(amount) + Number(this.chargeDetails?this.chargeDetails[0].amount:'');
        console.log(this.totalTransAmount);
        this.chequedepositForm.controls.totalTransactionAmount.setValue(this.totalTransAmount);
      }, 1000);
      this.cdr.markForCheck();
      this.cdr.detectChanges();
    }
  
      // console.log(this.totalTransAmount);
    
    }
 

    addCurrencyImage(currency) {
      return currency.toLowerCase();
    }

    clicked(event){
      console.log(event);
    }
  
    onCountryChange(country) {
      let currency = country.currencyCode;
      this.currencyImage = 'currency-flag-'+currency.toLowerCase();
    }
  
    accountCurrencyImage(accountCurrency){
      return accountCurrency? 'currency-flag-'+accountCurrency.toLowerCase() : '';
    }
  
  formatAmount(amount) {
    if(amount){
      return amount && amount.toString().split('.')[0].length > 3 ? amount.toString().substring(0,amount.toString().split('.')[0].length-3).replace(/\B(?=(\d{2})+(?!\d))/g, ",") + "," + amount.toString().substring(amount.toString().split('.')[0].length-3): amount.toString();
     }
  }
  cashWithdrawlData(){
    this.apiService.getAppointmentId(this.appointmentId.data.appointmentId).subscribe((resp : any)=> {
      console.log(resp);
      this.cashData=resp.data;
      this.custID=resp.data.customerId;
      this.ifQRvariable = true;
      this.chequedepositForm.controls.accountType.setValue(resp.data.accountType);
      this.chequedepositForm.controls.accountBranch.setValue(resp.data.accountBranch);
      this.chequedepositForm.controls.accountNumber.setValue(resp.data.accountNumber);
      this.chequedepositForm.controls.chequeNumber.setValue(resp.data.chequeNumber);
      this.getChargeDetails(resp.data.accountNumber,resp.data.transactionAmount);
      this.chequedepositForm.controls.accountCurrency.setValue(resp.data.accountCurrency);
      this.chequedepositForm.controls.transactionCurrency.setValue(resp.data.transactionCurrency);
      this.chequedepositForm.controls.transactionAmount.setValue(resp.data.transactionAmount);
      this.currencyImage = 'currency-flag-'+resp.data.accountCurrency.toLowerCase();
      this.currencyImage = 'currency-flag-'+resp.data.transactionCurrency.toLowerCase();
  
  
    });
  }
  
