import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { verifyotpModel } from 'app/shared/models/otp.model';
import { ApiService } from 'app/shared/services/api.service';

@Component({
  selector: 'app-otp-verify',
  templateUrl: './otp-verify.component.html',
  styleUrls: ['./otp-verify.component.scss']
})
export class OtpVerifyComponent implements OnInit {
  errormsg : string;
  basicInformationForm: FormGroup;
  public number;
  public type;
  title: string;
  customerPhoneNum: string;
  verifyOtpModel = new verifyotpModel();
  @Output() afterSubmit : EventEmitter<boolean> = new EventEmitter();
  
  constructor(private dialogRef: MatDialogRef<OtpVerifyComponent>, private apiService: ApiService,
    private snack: MatSnackBar,private fb: FormBuilder,private router: Router,
    @Inject(MAT_DIALOG_DATA) private data: any) { }

  ngOnInit(): void {
    this.title = "An OTP has been sent to the Registered Mobile Number!";
    this.basicInformationForm = this.fb.group({
      otp: ['',[Validators.required]],
    
    })
  }
  closeClick(){
    this.dialogRef.close();
  }
  validateOtp(otpValue) {
    console.log("OTP validaion", otpValue);
    if(otpValue.otpNumber =="")
    {
      this.snack.open(`Please enter OTP to continue`, 'OK', {
        duration: 4000,
        verticalPosition: 'bottom',
        horizontalPosition: 'right'
      }); 
    }
    else{
    this.customerPhoneNum = localStorage.getItem("phonenumFromLogin");
    console.log("Phonenumber from Local", this.customerPhoneNum, otpValue);
    this.verifyOtpModel.sourceKey = 'mobile';
    this.verifyOtpModel.sourceValue = this.customerPhoneNum;
    this.verifyOtpModel.otp = otpValue.otp;
    this.verifyOtpModel.type = '';
    this.verifyOtpModel.isMobileLogin= false;
    console.log("model", this.verifyOtpModel);
    this.apiService.verifyOtp(this.verifyOtpModel).subscribe(otpResp => {
      console.log('otpResp :: ',otpResp)
      if (otpResp.data != null) {
        if(otpResp.data.userId != '' && otpResp.data.userId !=null && otpResp.data.status =='SUCCESS' ){
          this.afterSubmit.emit(true);
          localStorage.setItem('userId',otpResp.data.userId);
          this.snack.open(`Validation successful`, 'OK', {
            duration: 1000,
            verticalPosition: 'bottom',
            horizontalPosition: 'right'
          });
          this.dialogRef.close(true);
        }else{
          this.dialogRef.close(false);
          this.snack.open(`${otpResp.data.responseMessage}`, 'OK', {
            duration: 4000,
            verticalPosition: 'bottom',
            horizontalPosition: 'right'
          });
        }
      }
    },
      (error) => {
        console.log("wrong otp",error.status);
        // this.errorStatus = error.status;
        this.snack.open(`Incorrect OTP. Please try again.`, 'OK', {
          duration: 4000,
          verticalPosition: 'bottom',
          horizontalPosition: 'right'
        });
        // if (this.errorStatus == 200) {
        //   // this.router.navigateByUrl('/others/services');
        //   /* TO-DO: need to uncomment this for fingerprint verification */
        //   // this.openDialog();
        // }
      });
    }
  }
}
