  formatAmount(amount) {
    // amount=amount.toString();
    // var lastThree = amount.substring(amount.length - 3);
    // var otherNumbers = amount.substring(0,amount.length-3);
    // if(otherNumbers != '')
    //     lastThree = ',' + lastThree;
    // return String(amount).replace(/\B(?=(\d{3})+(?!\d))/g, ",")+ lastThree;
    // return String(amount).replace(en-IN,',');
    if (amount) {
      return amount && amount.toString().split('.')[0].length > 3
        ? amount
          .toString()
          .substring(0, amount.toString().split('.')[0].length - 3)
          .replace(/\B(?=(\d{2})+(?!\d))/g, ',') +
        ',' +
        amount
          .toString()
          .substring(amount.toString().split('.')[0].length - 3)
        : amount.toString();
    }
  }
  setAppointmentIdData() {
    this.apiService
      .getAppointmentId(this.appointmentId.data.appointmentId)
      .subscribe((resp: any) => {
        console.log("RESP", resp);
        this.cashData = resp.data;

        this.custID = resp.data.customerId;
        this.ifQRvariable = true;
        this.hideAccNum = true;
        this.hideDetails = true;
        this.showzero = false
        this.flag1 = true;
        this.displayFlag = true;
        this.accountDropdown = true;
        this.currencyCode = resp.data.accountCurrency;
        this.loanForm.controls.loanType.setValue(resp.data.accountType);
        this.loanForm.controls.loanAccountId.setValue(resp.data.modeInfo[0].loanAccountNo);
        this.loanForm.controls.customerName.setValue(resp.data?.holderName);
        this.loanForm.controls.accountBranch.setValue(resp.data.accountBranch);
        this.loanForm.controls.accountCurrency.setValue(resp.data?.accountCurrency);

        this.fetchDisbursementAccInfo();

        if (this.titleVal == 'Repayment') {
          this.loanForm.controls.principalOutstanding.setValue(resp.data.loanInfo[0]?.principalOutstanding);
          this.loanForm.controls.currentArrears.setValue(resp.data.loanInfo[0]?.currentArrears);
        }
        if (this.titleVal == 'Disbursement') {
          this.loanForm.controls.sanctionedAmount.setValue(
            resp.data.loanInfo[0].sanctionedAmount
          );
          this.loanForm.controls.disbursedAmount.setValue(
            resp.data.loanInfo[0].disbursedAmount
          );
        }
        // this.loanForm.controls.currentDue.setValue(resp.data.loanInfo[0]?.currentDue);

        console.log(resp.data.accountCurrency);
        this.dataService.sendloanRepaymentData(this.cashData);
      });
    console.log(this.cashData);



    // this.denomRequired='yes';
  }

  fetchDisbursementAccInfo() {
    this.apiService.fetchDisbursementAccInfo(this.custID).subscribe((res: any) => {
      if (res?.status == 200) {
        let accountList = res?.data.reverse();
        this.ls.setItem("DISB_ACNT", accountList);
      } else {
        this.ls.setItem("DISB_ACNT", []);
      }
    }, (err: any) => {
      this.snack.open(err);
    })
  }

