import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { OcrScanPageComponent } from '../ocr-scan-page/ocr-scan-page.component';

@Component({
  selector: 'app-capture-cheque-details',
  templateUrl: './capture-cheque-details.component.html',
  styleUrls: ['./capture-cheque-details.component.scss']
})
export class CaptureChequeDetailsComponent implements OnInit {
  flag: boolean = false ;
  flag1: boolean = false ;
  capturedImageFront: any;
  capturedImageBack: any;

  constructor(private dialog: MatDialog,public dialogRef: MatDialogRef<CaptureChequeDetailsComponent>,private router: Router) { }

  ngOnInit(){
  }

  scanChequeFront(){
    console.log("front click");
    let dialogRef = this.dialog.open(OcrScanPageComponent, {
      width: '100%',
      height:'80%',
    });
    dialogRef.afterClosed().subscribe(result => {
      this.capturedImageFront = sessionStorage.getItem('sysImage')
      this.flag = true;
      console.log("1",this.flag);
    });
  }

  scanChequeBack(){
    console.log("back click");
    let dialogRef = this.dialog.open(OcrScanPageComponent, {
      width: '100%',
      height:'80%',
    });
    dialogRef.afterClosed().subscribe(result => {
      this.capturedImageBack = sessionStorage.getItem('sysImage')
      this.flag1 = true;
      console.log("1",this.flag1);
    });
  }

  back(){
    this.dialogRef.close();
  }

  cancel(){
    this.dialogRef.close();
  }

  submit(){
    this.dialogRef.close();
  }
}
