import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { DialogService } from 'app/shared/services/Dialog/dialog.service';

interface Testing {
  value: string;
  viewValue: string;
}

@Component({
  selector: 'app-deposit-topup',
  templateUrl: './deposit-topup.component.html',
  styleUrls: ['./deposit-topup.component.scss']
})
export class DepositTopupComponent implements OnInit {
  depositForm: FormGroup;
  depositTopUp : any = 'Deposit Top Up';
  constructor(private router : Router,private formBuilder: FormBuilder, private dialogService : DialogService) { }

  ngOnInit(): void {
    this.depositForm = this.formBuilder.group({
      depositAccount:['', [Validators.required]],
      revisedAmount:['', [Validators.required]],
      maturityAmount:['', [Validators.required]],
      payFromAccount:['', [Validators.required]],
      holderName:['', [Validators.required]],
      topupAmount:['', [Validators.required]], 
    });
  }
  goToServiceScreens(){
    this.router.navigateByUrl('/others/kioskHome');
  }
  goToAuthenticate(){
    this.dialogService.authenticationDialog();
  }

  testing: Testing[] = [
    { value: 'Demo', viewValue: 'Demo 1' },
    { value: 'Demo', viewValue: 'Demo 2' },
    { value: 'Demo', viewValue: 'Demo 3' }
  ];


}
