import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { DialogService } from "app/shared/services/Dialog/dialog.service";

@Component({
  selector: "app-footer",
  templateUrl: "./footer.component.html",
  styleUrls: ["./footer.component.scss"],
})
export class FooterComponent implements OnInit {
  showAccountNum: boolean = true;
  @Input() public title: string = "Form";
  @Input() public saveText = "Continue";
  @Input() public cancelText = "Cancel";
  @Input() public cancelDisabled = false;
  @Input() public saveDisabled = false;
  @Input() public saving = false;

  @Output() public cancel = new EventEmitter<void>();
  @Output() public save = new EventEmitter<void>();
  @Output() public exit = new EventEmitter<void>();
  custNameFromLogin: string;
  custValidation: string;
  chqPage: string;

  constructor(private dialogService: DialogService) {}

  ngOnInit(): void {
    console.log("Inside Footer");
    this.chqPage = localStorage.getItem('chq_page');
    this.custValidation = localStorage.getItem("customerValidation");
    this.custNameFromLogin = localStorage.getItem("FromLogin");
    console.log(this.custValidation,this.custNameFromLogin);
    if (this.custValidation == "New Customer") {
      this.showAccountNum = false;
    }
    if (this.custNameFromLogin != null) {
      this.showAccountNum = false;
    }
  }
  clickExit() {
    this.dialogService.clickExit();
  }
}
