  ngOnInit(): void {
    this.custValidation = localStorage.getItem("customerValidation");
    this.transBranch = localStorage.getItem("TRANSACTIONBRANCH");
    this.denomRequired = localStorage.getItem("DenominationRequired");
    this.buildDenomForm();

    this.loanDisburshmentForm = this.formBuilder.group({
      loanInfo: [],
      paymentInfo: [],
      denomination: [],
      entityCode: [this.entityCode, [Validators.required]],
      bankCode: [this.bankCode, [Validators.required]],
      tokenBranch: [this.tokenBranch, [Validators.required]],
      customerCategory: [this.custPref, [Validators.required]],
      tokenNumber: ['', [Validators.required]],
      productCode: ['LND',
        Validators.required
      ],
      tokenOrigin: ['Kiosk',
        Validators.required
      ],
      estimatedWaitTime: ['', [Validators.required]],
      narrative: ['', [Validators.required]],
    });

    this.dataService.getpaymentInfoFormdata.subscribe(payment => {
      console.log("state data for payment", payment)
      if (payment) {
        this.paymentData = payment;
        this.displayDenom = this.paymentData.rePaymentMode == 'Cash' ? true : false;
        this.denomData = this.paymentData?.denomination;
        if (this.paymentData?.denomination != null) {
          this.populateDenomination();
        } else {
          this.denominationForm.patchValue([]);
        }
        console.log(this.displayDenom)

        // this.cdr.markForCheck();
      }
    });

    this.dataService.getdenominationFormdata.subscribe(denomination => {
      console.log("state data for denomination", denomination)
      if (denomination) {
        console.log("Account component?", denomination);
        this.denominationdata = denomination;

      }

    })
    this.dataService.getloanRepaymentData.subscribe((payment: any) => {
      console.log("state data for payment::::", payment)
      if (payment) {
        this.appointmentDetails = payment;
        this.displayDenom = payment?.modeInfo[0]?.rePaymentMode == 'Cash' ? true : false;
        console.log(this.displayDenom)
      }
    });

    this.denominationForm.valueChanges.subscribe(val => {
      let sum: number = 0;
      Object.keys(this.denominationForm.controls).forEach(key => {
        for (let index = 0; index < this.denomination_keys.length; index++) {
          const denomItem = this.denomination_keys[index];
          if (denomItem.key == key) {
            sum += denomItem.value * Number(this.denominationForm.get(`${key}`)?.value);
          }
        }
      });
      this.total = sum;
    });
  }
  // DENOM SECTION
  calculateTotalAmount() {
    let sum = 0;
    Object.keys(this.denominationForm.controls).forEach(key => {
      for (let index = 0; index < this.denomination_keys.length; index++) {
        const denomItem = this.denomination_keys[index];
        if (denomItem.key == key) {
          sum += denomItem.value * Number(this.denominationForm.get(`${key}`)?.value);
        }
      }
    });

    this.total = sum;
  }

  populateDenomination() {
    this.denomination_keys.forEach((key, i) => {
      for (let index = 0; index < this.denomData.length; index++) {
        const denomItem = this.denomData[index];
        if (denomItem.denominationKey === key.denom_key) {
          this.denominationForm.get(key?.key)?.patchValue(denomItem?.denominationValue);
        }
      }
    });

    this.calculateTotalAmount();
  }

  // DENOM SECTION

