import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Location } from '@angular/common';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { Router } from '@angular/router';
import { DataService } from 'app/shared/services/data.service';
import { MatDialog } from '@angular/material/dialog';
import { ScanYourChequeComponent } from 'app/views/others/scan-your-cheque/scan-your-cheque.component';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  @Input() public title: string = null;
  @Input() public screen:string;
  @Output() public back = new EventEmitter<void>();
  appointmentId: any;
  transactiondata: any;

  accountData: any;
  chqPage: string;
  denominationdata: any;
  custValidation: string;
  custNameFromLogin: string;
  titleData: any;
  paymentData:any;
  denominationRequired: string;
  
  constructor(private dialog: MatDialog,private _location: Location,
    private ls: LocalStoreService,private router : Router,private dataService:DataService) { }

  ngOnInit(): void {
    console.log("Inside Header",this.back)
    this.chqPage = localStorage.getItem('chq_page');
    this.appointmentId = this.ls.getItem("TransactionType");
    this.custValidation = localStorage.getItem("customerValidation");
    this.custNameFromLogin = localStorage.getItem("FromLogin");
    this.denominationRequired = localStorage.getItem("DenominationRequired");
    this.dataService.gettransactionFormdata.subscribe(transaction=>{
      if(Object.keys(transaction).length != 0  && transaction.constructor === Object){
        this.transactiondata=transaction;
        console.log(this.transactiondata);
       
      }
 
    });
    this.dataService.getaccountFormdata.subscribe(account=>{
      console.log(Object.keys(account).length === 0,account.constructor === Object)
      if(Object.keys(account).length != 0  && account.constructor === Object){
        this.accountData=account;
      }
 
    });
    this.dataService.getdenominationFormdata.subscribe(denomination=>{
      if(denomination){
        this.denominationdata=denomination;
      }
    })
    this.dataService.getNavParam.subscribe((title) => {
      console.log(title);
      this.titleData = title;
    });
    this.dataService.getpaymentInfoFormdata.subscribe((title) => {
      console.log(title);
      this.paymentData = title;
    });
  }
  goToServiceScreens(){
    console.log("hgfhg");
    if(this.appointmentId){
    if(this.appointmentId.data.appointmentId){
      this.router.navigateByUrl('/sessions/login');
      localStorage.removeItem("FromLogin")
      localStorage.removeItem("TransactionType")
      localStorage.removeItem("customerId")
      localStorage.removeItem("phonenumFromLogin")
      localStorage.removeItem("customerValidation")
      localStorage.removeItem("otpPhoneNum")
      localStorage.removeItem("AccountBal")
      localStorage.removeItem("AccountNumber")
      localStorage.removeItem("ProductCode")
      localStorage.removeItem("tokenNumer")
      localStorage.removeItem("estimatedTiming")
      localStorage.removeItem("userId")
      localStorage.removeItem("scannerData")
      localStorage.removeItem("custholderName")
    }
  }
    else{
      this.router.navigateByUrl('/others/kioskHome');
    }
    
  } 

  scanTheCheque(){
    let dialogRef = this.dialog.open(ScanYourChequeComponent, {
      width: '60%',
      height:'65%',
    });
}
scanCurency(){
  this.router.navigateByUrl('others/currency-converter');
}

  back1() {
    this.router.navigateByUrl('others/kioskHome');
    console.log("Logssss")
    this.dataService.removetransactionFormData(this.transactiondata);
    this.dataService.removeaccountFormData(this.accountData);
    this.dataService.removedenominationFormData(this.denominationdata);
    this.dataService.removeNavParam(this.titleData);
    this.dataService.removePaymentInfoFormData(this.paymentData);
    // this._location.back();
  }

  backButton() {
    this.dataService.removetransactionFormData(this.transactiondata);
    this.dataService.removeaccountFormData(this.accountData);
    this.dataService.removedenominationFormData(this.denominationdata);
    this.dataService.removeNavParam(this.titleData);
    this.dataService.removePaymentInfoFormData(this.paymentData);
    
    this._location.back();
  }

}
