  setAppointmentIdData(data) {
    console.log(data);
    this.custID = data.customerId;
    this.ifQRvariable = true;
    this.hideAccNum = false;
    this.hideDetails = true;
    this.userName = data.holderName;
    this.displayNegotiated = false;
    this.accountCurrency = data.accountCurrency;
    this.chargeAmount = data?.totalChargeAmount ?? this.chargeAmount;
    this.selectedMode = data.modeInfo[0].rePaymentMode;
    this.cdr.markForCheck();



    this.paymentForm.controls.rePaymentMode.patchValue(this.selectedMode);
    console.log(this.paymentForm)
    this.paymentForm.controls.transactionCurrency.patchValue(
      data.transactionCurrency ? data.transactionCurrency : ''
    );

    if (this.accountCurrency != this.paymentForm.controls.transactionCurrency.value) {
      this.displayNegotiated = true;
    }

    this.currencyImage = "currency-flag-" + data?.transactionCurrency.toLowerCase();
    this.paymentForm.controls.transactionAmount.patchValue(
      data.transactionAmount
    );
    this.staticExcangeRate = data?.exchangeRate;
    this.paymentForm.get('exchangeRate')?.patchValue(data?.exchangeRate);

    if (data.modeInfo[0].rePaymentMode == 'Cheque') {
      this.chequeType = data.modeInfo[0].chequeType;
      this.paymentForm.controls.chequeType.patchValue(
        data.modeInfo[0].chequeType
      );
      this.paymentForm.controls.nameOnCheque.patchValue(
        data.modeInfo[0].nameOnCheque
      );
      this.paymentForm.controls.chequeNumber.patchValue(
        data.modeInfo[0].chequeNo
      );
    }

    if (data.modeInfo[0].rePaymentMode == 'Account') {
      this.selectedPaymentAccount = data.modeInfo[0]?.accountNumber;
      this.paymentForm.get('accountNumber')?.patchValue(data.modeInfo[0]?.accountNumber);
      this.paymentForm.controls.accountCurrency.patchValue(data.modeInfo[0].accountCurrency);
      this.paymentForm.controls.accountBalance.patchValue(data.modeInfo[0].accountBalance ? data.modeInfo[0].accountBalance : 0);
      this.paymentForm.controls.holderName.patchValue(data.holderName);

      this.paymentForm.controls.accountBranch.patchValue(
        data.modeInfo[0].accountBranch
      );
    }

    if (data.modeInfo[0].rePaymentMode == 'Cash') {
      this.paymentForm.controls.denomination.patchValue(
        data?.denomination
      );
    }
    console.log(this.currencyImage);
    this.getChargeDetails(data.accountNumber, "");

  }
  onCountryChange(country) {
    let currency = country.currencyCode;
    console.log(this.paymentForm)
    this.currencyImage = "currency-flag-" + currency.toLowerCase();
    // this.fetchExchangeRateForCurrency(currency);
    if (this.accountCurrency != this.paymentForm.controls.transactionCurrency.value) {
      this.displayNegotiated = true;
      this.fetchExchangeRateForCurrency(currency, this.accountCurrency);
    }
    else {
      this.displayNegotiated = false;
    }
  }


  setWalkinCustomerData(walkinData) {
    this.accountCurrency = walkinData[0].accountCurrency;
    this.paymentForm.controls.transactionAmount.patchValue(walkinData[0]?.currentArrears ? walkinData[0]?.currentArrears : 0);
    this.paymentForm.controls.transactionCurrency.patchValue(walkinData[0].accountCurrency);
    this.currencyImage = 'currency-flag-' + walkinData[0].accountCurrency.toLowerCase();
    console.log(this.paymentForm);
  }

