  getChargeDetails(accountId, amount) {
    this.apiService.fetchChargeDetails(accountId).subscribe((resp) => {
    
      this.chargeDetails = resp;
      console.log( this.chargeDetails);
      this.transactionForm.controls.totalChargeAmount.setValue(
        this.chargeDetails ? this.chargeDetails[0].amount : ""
      );
    });
 
    if (this.cashData) {
      setTimeout(() => {
       
        if( this.transactionForm.controls.transactionCurrency.value==this.accountCurrency){
          console.log(this.accountCurrency)
          this.totalTransAmount =
          Number(this.cashData.transactionAmount) +
          Number(this.chargeDetails[0].amount);
        this.transactionForm.controls.totalTransactionAmount.setValue(
          this.totalTransAmount
        );
        }
        if( this.transactionForm.controls.transactionCurrency.value!=this.accountCurrency){
          console.log(this.accountCurrency)
          this.totalTransAmount =
          (Number(this.cashData.transactionAmount)* Number(this.transactionForm.controls.negotiatedRate.value ?this.transactionForm.controls.negotiatedRate.value:this.transactionForm.controls.exchangeRate.value))+
          Number(this.chargeDetails[0].amount);
          console.log("line 845 getchrgedetails",this.totalTransAmount);
          this.transactionForm.controls.totalTransactionAmount.setValue(
          this.totalTransAmount
        );
        }
     
      }, 1000);
      this.cdr.markForCheck();
      this.cdr.detectChanges();
    } else {
      console.log(this.chargeDetails);
      amount = 0;
      console.log(this.transactionForm.controls.totalChargeAmount.value);
      setTimeout(() => {
        this.totalTransAmount =
          Number(this.transactionForm.controls.transactionAmount.value?this.transactionForm.controls.transactionAmount.value:amount) +
          Number(this.chargeDetails ? this.chargeDetails[0].amount : "");
        console.log(this.totalTransAmount);
        this.transactionForm.controls.totalTransactionAmount.setValue(
          this.totalTransAmount
        );
      }, 1000);
      this.cdr.markForCheck();
      this.cdr.detectChanges();
    }

    // console.log(this.totalTransAmount);
  }
