    getOTP(formData, type, cdForm) {
      this.phonenum=this.ls.getItem('phonenumFromLogin');
      this.chequedepositValues = formData;
      this.chequedepositype = type;
      this.cheqDepositForm = cdForm;
      this.oTpModel.source = "customer";
      this.oTpModel.source_key = "mobile";
      this.oTpModel.source_value = this.phonenum;
      this.oTpModel.isMobileLogin = false;
      let amount = 50000;
      let transamountwithoutcomma = this.chequedepositValues.transaction.transactionAmount.replace(
        /,/g,
        ""
      );
      console.log(transamountwithoutcomma);
      if (transamountwithoutcomma >= amount) {
        this.tokendisable=true;
        this.apiService.getOtp(this.oTpModel).subscribe((otpResp) => {
          if (otpResp) {
            this.openDialogOTP(this.phonenum);
            this.snack.open(
              "An OTP has been sent to the Registered Mobile Number.",
              "",
              {
                duration: 4000,
                verticalPosition: "top",
                horizontalPosition: "right",
              }
            );
          }
        });
      } else {
        this.getOTPforSave(
          this.chequedepositValues,
          this.chequedepositype,
          this.cheqDepositForm
        );
      }
    }
    openDialogOTP(numbr) {
      const dialogRef = this.dialog.open(OtpVerifyComponent, {
        data: { number: numbr, type: "numbr" },
        disableClose: true,
      });
      dialogRef.afterClosed().subscribe((submit) => {
        if (submit) {
          this.getOTPforSave(
            this.chequedepositValues,
            this.chequedepositype,
            this.cheqDepositForm
          );
        }
      });
    }
   
