export class LoanTokenGenerationObj {
  transactionId: any;
  customerId: any;
  holderName: any;
  customerName: any;
  accountNumber: any;
  accountType: any;
  remarks: any;
  accountCurrency: any;
  transactionCurrency: any;
  transactionAmount: any;
  accountBalance: any;
  productCode: any;
  tokenOrigin: any;
  exchangeRate: any;
  accountAmount: any;
  totalChargeAmount: any;
  totalTransactionAmount: any;
  narrative: any;
  denomination: any[];
  modeInfo: any[];
  appointmentId: any;
  appointmentStatus: any;
  // loanRepaymentModeInfo: loanRepaymentModeInfo[];
  totalAmount: any;
  entityCode: any;
  bankCode: any;
  tokenBranch: any;
  customerCategory: any;
  tokenNumber: any;
  estimatedWaitTime: any;
  accountBranch: any;
  transactionBranch: any;
  transactionStatus: any;
  nameOfTheCheque: any
  chequeType: any;
  negotiatedRate: any;
}