import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatSnackBar } from "@angular/material/snack-bar";
import { Router } from "@angular/router";
import { ApiService } from "app/shared/services/api.service";
import { DataService } from "app/shared/services/data.service";
import { DialogService } from "app/shared/services/Dialog/dialog.service";
import { LocalStoreService } from "app/shared/services/local-store.service";
import { Location } from "@angular/common";
import { otpModel } from "app/shared/models/otp.model";
@Component({
  selector: "app-add-account-disburshment",
  templateUrl: "./add-account-disburshment.component.html",
  styleUrls: ["./add-account-disburshment.component.scss"],
})
export class AddAccountDisburshmentComponent implements OnInit {
  addAccountDisburshment: FormGroup;
  title = "Add Account";
  trnTypeLoan: any = "Internal";
  displayFlag: boolean = true;
  //trnTypeMode:any;
  trnTypeMode: trnTypeMode[] = [
    { mode: "Internal", viewValue: "Internal", display: false, checked : true },
    { mode: "Other Bank", viewValue: "Other Bank", display: false , checked : false },
  ];
  currentUser: string;
  constructor(
    private router: Router,
    private formBuilder: FormBuilder,
    private _location: Location,
    private dataService: DataService,
    private apiService:ApiService, private snack: MatSnackBar
  ) {}

  ngOnInit(): void {
    console.log(this.trnTypeMode);
    this.currentUser=localStorage.getItem("customerId");
    this.addAccountDisburshment = this.formBuilder.group({
      transferType:[this.trnTypeLoan,[Validators.required]],
      accountNumber:['',[Validators.required]],
      customerName:['',[Validators.required]],
      bankCode:[''],
      customerNickName:['',[Validators.required]],
      accountCurrency:['INR',[Validators.required]],
      customerId:[this.currentUser,[Validators.required]]
    })
    this.addAccountDisburshment.get('transferType')?.valueChanges.subscribe(val => {
      if (val === 'Internal') {
        this.addAccountDisburshment.get('bankCode')?.patchValue("");
        this.addAccountDisburshment.get('bankCode').clearValidators(); // 6. Clear All Validators
        this.addAccountDisburshment.get('bankCode').updateValueAndValidity();
      } else {
        this.addAccountDisburshment.get('bankCode').setValidators([Validators.required]); // Set Required Validator
        this.addAccountDisburshment.get('bankCode').updateValueAndValidity();
      }
    })
    // this.buildAddAccountForm();
  }
  public redirectToHome(): void {
    console.log("Redirect");
    this.router.navigateByUrl("/others/loan-disburshment");
  }
 
  onSubmit(data) {
    // this.isSubmitted = true;
    if (!this.addAccountDisburshment.valid) {
      return false;
    } else {
      console.log(this.addAccountDisburshment.value);
      this.apiService.addNewAccountToDisburse(this.addAccountDisburshment.value).subscribe(res => {
        console.log(res);
        if (res.status == 200) {
          // this.close();
          this.snack.open(res?.message,
            // {
            //   duration: 4000,
            //   verticalPosition: "top",
            //   horizontalPosition: "right",
            // }
          );
          // this.toastService.showToast(res?.message);
        } else {
          this.snack.open(
            res?.message,
            // {
              // duration: 4000,
            //   verticalPosition: "top",
            //   horizontalPosition: "right",
            // }
          );
          // this.toastService.showToast(res?.message);
        }
      }, (err: any) => {
        this.snack.open(err);
      });
    }
  }
  // buildAddAccountForm(){
  //   this.addAccountDisburshment = this.formBuilder.group({
  //     trnTypeLoan:[''],
  //     custAcc:[''],
  //     custName:[''],
  //     bankCode:[''],
  //     custNickName:['']
  //   })
  // }
  public redirectToHome1(): void {
    console.log("Redirect");
    this.router.navigateByUrl("/others/kioskHome");
  }
  changeTransType(event) {
    console.log(event);
    
    this.trnTypeLoan = event;
  }

}
interface trnTypeMode {
  mode: string;
  viewValue: string;
  display: boolean;
  checked:boolean
}
